.rfipbtn--deeporange {
  background-color: #ffffff;
  border: 1px solid #ff8a65;
}
.rfipbtn--deeporange:active, .rfipbtn--deeporange:focus {
  box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24), 0 1.5px 6px rgba(0, 0, 0, 0.12);
  border: 1px solid #ffab91;
}
.rfipbtn--deeporange .rfipbtn__button {
  border: 0 none transparent;
  border-left: 1px solid #ff8a65;
  background-color: #ffccbc;
  color: #d84315;
}
.rfipbtn--deeporange .rfipbtn__button:hover {
  background-color: #ffab91;
}
.rfipbtn--deeporange .rfipbtn__button:active {
  box-shadow: inset 0 0 10px 0 #ff8a65;
}
.rfipbtn--deeporange .rfipbtn__selection {
  position: inherit;
}
.rfipbtn--deeporange .rfipbtn__icon {
  border: 1px solid #ffab91;
  color: #d84315;
}
.rfipbtn--deeporange .rfipbtn__icon--empty {
  color: #ff8a65;
}
.rfipbtn--deeporange .rfipbtn__del {
  background-color: #ffab91;
}
.rfipbtn--deeporange .rfipbtn__del:hover {
  background-color: #ff8a65;
}
.rfipbtn--deeporange .rfipbtn__del:focus, .rfipbtn--deeporange .rfipbtn__del:active {
  outline: 1px solid #ff8a65;
}

.rfipdropdown--deeporange {
  box-shadow: 0 15px 24px rgba(0, 0, 0, 0.22), 0 19px 76px rgba(0, 0, 0, 0.3);
  color: #424242;
  background-color: #ffffff;
  border: 1px solid #ff8a65;
}
.rfipdropdown--deeporange input,
.rfipdropdown--deeporange select {
  color: #424242;
}
.rfipdropdown--deeporange .rfipcategory select {
  background-color: #ffffff;
  border: 0 none;
  border-bottom: 1px solid #ff7043;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--deeporange .rfipcategory select:focus, .rfipdropdown--deeporange .rfipcategory select:active {
  border-bottom-color: #ff5722;
  box-shadow: 0 1px 0 0 #ff5722;
  outline: 0 none;
}
.rfipdropdown--deeporange .rfipicons__cp {
  border: 0 none;
  border-bottom: 1px solid #ff7043;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--deeporange .rfipicons__cp:focus, .rfipdropdown--deeporange .rfipicons__cp:active {
  border-bottom-color: #ff5722;
  box-shadow: 0 1px 0 0 #ff5722;
  outline: 0 none;
}
.rfipdropdown--deeporange .rfipicons__left, .rfipdropdown--deeporange .rfipicons__right {
  background-color: #ffab91;
  border: 1px solid #ffab91;
  color: #d84315;
}
.rfipdropdown--deeporange .rfipicons__left:hover, .rfipdropdown--deeporange .rfipicons__right:hover {
  background-color: #ff7043;
  border: 1px solid #ff7043;
}
.rfipdropdown--deeporange .rfipicons__left:focus, .rfipdropdown--deeporange .rfipicons__left:active, .rfipdropdown--deeporange .rfipicons__right:focus, .rfipdropdown--deeporange .rfipicons__right:active {
  border: 1px solid #ff7043;
}
.rfipdropdown--deeporange .rfipicons__ibox {
  background-color: #ffccbc;
  border: 1px solid #ffccbc;
  color: #d84315;
}
.rfipdropdown--deeporange .rfipicons__ibox:hover {
  background-color: #ff7043;
  border: 1px solid #ff7043;
}
.rfipdropdown--deeporange .rfipicons__ibox:focus, .rfipdropdown--deeporange .rfipicons__ibox:active {
  border: 1px solid #ff7043;
}
.rfipdropdown--deeporange .rfipicons__ibox--error {
  color: #ef9a9a;
}
.rfipdropdown--deeporange .rfipicons__icon--selected .rfipicons__ibox {
  background-color: #ffab91;
}
.rfipdropdown--deeporange .rfipsearch input {
  border: 0 none;
  border-bottom: 1px solid #ff7043;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--deeporange .rfipsearch input:focus, .rfipdropdown--deeporange .rfipsearch input:active {
  border-bottom-color: #ff5722;
  box-shadow: 0 1px 0 0 #ff5722;
  outline: 0 none;
}