.rfipbtn--teal {
  background-color: #ffffff;
  border: 1px solid #4db6ac;
}
.rfipbtn--teal:active, .rfipbtn--teal:focus {
  box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24), 0 1.5px 6px rgba(0, 0, 0, 0.12);
  border: 1px solid #80cbc4;
}
.rfipbtn--teal .rfipbtn__button {
  border: 0 none transparent;
  border-left: 1px solid #4db6ac;
  background-color: #b2dfdb;
  color: #00695c;
}
.rfipbtn--teal .rfipbtn__button:hover {
  background-color: #80cbc4;
}
.rfipbtn--teal .rfipbtn__button:active {
  box-shadow: inset 0 0 10px 0 #4db6ac;
}
.rfipbtn--teal .rfipbtn__selection {
  position: inherit;
}
.rfipbtn--teal .rfipbtn__icon {
  border: 1px solid #80cbc4;
  color: #00695c;
}
.rfipbtn--teal .rfipbtn__icon--empty {
  color: #4db6ac;
}
.rfipbtn--teal .rfipbtn__del {
  background-color: #80cbc4;
}
.rfipbtn--teal .rfipbtn__del:hover {
  background-color: #4db6ac;
}
.rfipbtn--teal .rfipbtn__del:focus, .rfipbtn--teal .rfipbtn__del:active {
  outline: 1px solid #4db6ac;
}

.rfipdropdown--teal {
  box-shadow: 0 15px 24px rgba(0, 0, 0, 0.22), 0 19px 76px rgba(0, 0, 0, 0.3);
  color: #424242;
  background-color: #ffffff;
  border: 1px solid #4db6ac;
}
.rfipdropdown--teal input,
.rfipdropdown--teal select {
  color: #424242;
}
.rfipdropdown--teal .rfipcategory select {
  background-color: #ffffff;
  border: 0 none;
  border-bottom: 1px solid #26a69a;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--teal .rfipcategory select:focus, .rfipdropdown--teal .rfipcategory select:active {
  border-bottom-color: #009688;
  box-shadow: 0 1px 0 0 #009688;
  outline: 0 none;
}
.rfipdropdown--teal .rfipicons__cp {
  border: 0 none;
  border-bottom: 1px solid #26a69a;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--teal .rfipicons__cp:focus, .rfipdropdown--teal .rfipicons__cp:active {
  border-bottom-color: #009688;
  box-shadow: 0 1px 0 0 #009688;
  outline: 0 none;
}
.rfipdropdown--teal .rfipicons__left, .rfipdropdown--teal .rfipicons__right {
  background-color: #80cbc4;
  border: 1px solid #80cbc4;
  color: #00695c;
}
.rfipdropdown--teal .rfipicons__left:hover, .rfipdropdown--teal .rfipicons__right:hover {
  background-color: #26a69a;
  border: 1px solid #26a69a;
}
.rfipdropdown--teal .rfipicons__left:focus, .rfipdropdown--teal .rfipicons__left:active, .rfipdropdown--teal .rfipicons__right:focus, .rfipdropdown--teal .rfipicons__right:active {
  border: 1px solid #26a69a;
}
.rfipdropdown--teal .rfipicons__ibox {
  background-color: #b2dfdb;
  border: 1px solid #b2dfdb;
  color: #00695c;
}
.rfipdropdown--teal .rfipicons__ibox:hover {
  background-color: #26a69a;
  border: 1px solid #26a69a;
}
.rfipdropdown--teal .rfipicons__ibox:focus, .rfipdropdown--teal .rfipicons__ibox:active {
  border: 1px solid #26a69a;
}
.rfipdropdown--teal .rfipicons__ibox--error {
  color: #ef9a9a;
}
.rfipdropdown--teal .rfipicons__icon--selected .rfipicons__ibox {
  background-color: #80cbc4;
}
.rfipdropdown--teal .rfipsearch input {
  border: 0 none;
  border-bottom: 1px solid #26a69a;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--teal .rfipsearch input:focus, .rfipdropdown--teal .rfipsearch input:active {
  border-bottom-color: #009688;
  box-shadow: 0 1px 0 0 #009688;
  outline: 0 none;
}