.rfipbtn--deeppurple {
  background-color: #ffffff;
  border: 1px solid #9575cd;
}
.rfipbtn--deeppurple:active, .rfipbtn--deeppurple:focus {
  box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24), 0 1.5px 6px rgba(0, 0, 0, 0.12);
  border: 1px solid #b39ddb;
}
.rfipbtn--deeppurple .rfipbtn__button {
  border: 0 none transparent;
  border-left: 1px solid #9575cd;
  background-color: #d1c4e9;
  color: #4527a0;
}
.rfipbtn--deeppurple .rfipbtn__button:hover {
  background-color: #b39ddb;
}
.rfipbtn--deeppurple .rfipbtn__button:active {
  box-shadow: inset 0 0 10px 0 #9575cd;
}
.rfipbtn--deeppurple .rfipbtn__selection {
  position: inherit;
}
.rfipbtn--deeppurple .rfipbtn__icon {
  border: 1px solid #b39ddb;
  color: #4527a0;
}
.rfipbtn--deeppurple .rfipbtn__icon--empty {
  color: #9575cd;
}
.rfipbtn--deeppurple .rfipbtn__del {
  background-color: #b39ddb;
}
.rfipbtn--deeppurple .rfipbtn__del:hover {
  background-color: #9575cd;
}
.rfipbtn--deeppurple .rfipbtn__del:focus, .rfipbtn--deeppurple .rfipbtn__del:active {
  outline: 1px solid #9575cd;
}

.rfipdropdown--deeppurple {
  box-shadow: 0 15px 24px rgba(0, 0, 0, 0.22), 0 19px 76px rgba(0, 0, 0, 0.3);
  color: #424242;
  background-color: #ffffff;
  border: 1px solid #9575cd;
}
.rfipdropdown--deeppurple input,
.rfipdropdown--deeppurple select {
  color: #424242;
}
.rfipdropdown--deeppurple .rfipcategory select {
  background-color: #ffffff;
  border: 0 none;
  border-bottom: 1px solid #7e57c2;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--deeppurple .rfipcategory select:focus, .rfipdropdown--deeppurple .rfipcategory select:active {
  border-bottom-color: #673ab7;
  box-shadow: 0 1px 0 0 #673ab7;
  outline: 0 none;
}
.rfipdropdown--deeppurple .rfipicons__cp {
  border: 0 none;
  border-bottom: 1px solid #7e57c2;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--deeppurple .rfipicons__cp:focus, .rfipdropdown--deeppurple .rfipicons__cp:active {
  border-bottom-color: #673ab7;
  box-shadow: 0 1px 0 0 #673ab7;
  outline: 0 none;
}
.rfipdropdown--deeppurple .rfipicons__left, .rfipdropdown--deeppurple .rfipicons__right {
  background-color: #b39ddb;
  border: 1px solid #b39ddb;
  color: #4527a0;
}
.rfipdropdown--deeppurple .rfipicons__left:hover, .rfipdropdown--deeppurple .rfipicons__right:hover {
  background-color: #7e57c2;
  border: 1px solid #7e57c2;
}
.rfipdropdown--deeppurple .rfipicons__left:focus, .rfipdropdown--deeppurple .rfipicons__left:active, .rfipdropdown--deeppurple .rfipicons__right:focus, .rfipdropdown--deeppurple .rfipicons__right:active {
  border: 1px solid #7e57c2;
}
.rfipdropdown--deeppurple .rfipicons__ibox {
  background-color: #d1c4e9;
  border: 1px solid #d1c4e9;
  color: #4527a0;
}
.rfipdropdown--deeppurple .rfipicons__ibox:hover {
  background-color: #7e57c2;
  border: 1px solid #7e57c2;
}
.rfipdropdown--deeppurple .rfipicons__ibox:focus, .rfipdropdown--deeppurple .rfipicons__ibox:active {
  border: 1px solid #7e57c2;
}
.rfipdropdown--deeppurple .rfipicons__ibox--error {
  color: #ef9a9a;
}
.rfipdropdown--deeppurple .rfipicons__icon--selected .rfipicons__ibox {
  background-color: #b39ddb;
}
.rfipdropdown--deeppurple .rfipsearch input {
  border: 0 none;
  border-bottom: 1px solid #7e57c2;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--deeppurple .rfipsearch input:focus, .rfipdropdown--deeppurple .rfipsearch input:active {
  border-bottom-color: #673ab7;
  box-shadow: 0 1px 0 0 #673ab7;
  outline: 0 none;
}