.rfipbtn--lightblue {
  background-color: #ffffff;
  border: 1px solid #4fc3f7;
}
.rfipbtn--lightblue:active, .rfipbtn--lightblue:focus {
  box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24), 0 1.5px 6px rgba(0, 0, 0, 0.12);
  border: 1px solid #81d4fa;
}
.rfipbtn--lightblue .rfipbtn__button {
  border: 0 none transparent;
  border-left: 1px solid #4fc3f7;
  background-color: #b3e5fc;
  color: #0277bd;
}
.rfipbtn--lightblue .rfipbtn__button:hover {
  background-color: #81d4fa;
}
.rfipbtn--lightblue .rfipbtn__button:active {
  box-shadow: inset 0 0 10px 0 #4fc3f7;
}
.rfipbtn--lightblue .rfipbtn__selection {
  position: inherit;
}
.rfipbtn--lightblue .rfipbtn__icon {
  border: 1px solid #81d4fa;
  color: #0277bd;
}
.rfipbtn--lightblue .rfipbtn__icon--empty {
  color: #4fc3f7;
}
.rfipbtn--lightblue .rfipbtn__del {
  background-color: #81d4fa;
}
.rfipbtn--lightblue .rfipbtn__del:hover {
  background-color: #4fc3f7;
}
.rfipbtn--lightblue .rfipbtn__del:focus, .rfipbtn--lightblue .rfipbtn__del:active {
  outline: 1px solid #4fc3f7;
}

.rfipdropdown--lightblue {
  box-shadow: 0 15px 24px rgba(0, 0, 0, 0.22), 0 19px 76px rgba(0, 0, 0, 0.3);
  color: #424242;
  background-color: #ffffff;
  border: 1px solid #4fc3f7;
}
.rfipdropdown--lightblue input,
.rfipdropdown--lightblue select {
  color: #424242;
}
.rfipdropdown--lightblue .rfipcategory select {
  background-color: #ffffff;
  border: 0 none;
  border-bottom: 1px solid #29b6f6;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--lightblue .rfipcategory select:focus, .rfipdropdown--lightblue .rfipcategory select:active {
  border-bottom-color: #03a9f4;
  box-shadow: 0 1px 0 0 #03a9f4;
  outline: 0 none;
}
.rfipdropdown--lightblue .rfipicons__cp {
  border: 0 none;
  border-bottom: 1px solid #29b6f6;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--lightblue .rfipicons__cp:focus, .rfipdropdown--lightblue .rfipicons__cp:active {
  border-bottom-color: #03a9f4;
  box-shadow: 0 1px 0 0 #03a9f4;
  outline: 0 none;
}
.rfipdropdown--lightblue .rfipicons__left, .rfipdropdown--lightblue .rfipicons__right {
  background-color: #81d4fa;
  border: 1px solid #81d4fa;
  color: #0277bd;
}
.rfipdropdown--lightblue .rfipicons__left:hover, .rfipdropdown--lightblue .rfipicons__right:hover {
  background-color: #29b6f6;
  border: 1px solid #29b6f6;
}
.rfipdropdown--lightblue .rfipicons__left:focus, .rfipdropdown--lightblue .rfipicons__left:active, .rfipdropdown--lightblue .rfipicons__right:focus, .rfipdropdown--lightblue .rfipicons__right:active {
  border: 1px solid #29b6f6;
}
.rfipdropdown--lightblue .rfipicons__ibox {
  background-color: #b3e5fc;
  border: 1px solid #b3e5fc;
  color: #0277bd;
}
.rfipdropdown--lightblue .rfipicons__ibox:hover {
  background-color: #29b6f6;
  border: 1px solid #29b6f6;
}
.rfipdropdown--lightblue .rfipicons__ibox:focus, .rfipdropdown--lightblue .rfipicons__ibox:active {
  border: 1px solid #29b6f6;
}
.rfipdropdown--lightblue .rfipicons__ibox--error {
  color: #ef9a9a;
}
.rfipdropdown--lightblue .rfipicons__icon--selected .rfipicons__ibox {
  background-color: #81d4fa;
}
.rfipdropdown--lightblue .rfipsearch input {
  border: 0 none;
  border-bottom: 1px solid #29b6f6;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--lightblue .rfipsearch input:focus, .rfipdropdown--lightblue .rfipsearch input:active {
  border-bottom-color: #03a9f4;
  box-shadow: 0 1px 0 0 #03a9f4;
  outline: 0 none;
}