.rfipbtn--cyan {
  background-color: #ffffff;
  border: 1px solid #4dd0e1;
}
.rfipbtn--cyan:active, .rfipbtn--cyan:focus {
  box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24), 0 1.5px 6px rgba(0, 0, 0, 0.12);
  border: 1px solid #80deea;
}
.rfipbtn--cyan .rfipbtn__button {
  border: 0 none transparent;
  border-left: 1px solid #4dd0e1;
  background-color: #b2ebf2;
  color: #00838f;
}
.rfipbtn--cyan .rfipbtn__button:hover {
  background-color: #80deea;
}
.rfipbtn--cyan .rfipbtn__button:active {
  box-shadow: inset 0 0 10px 0 #4dd0e1;
}
.rfipbtn--cyan .rfipbtn__selection {
  position: inherit;
}
.rfipbtn--cyan .rfipbtn__icon {
  border: 1px solid #80deea;
  color: #00838f;
}
.rfipbtn--cyan .rfipbtn__icon--empty {
  color: #4dd0e1;
}
.rfipbtn--cyan .rfipbtn__del {
  background-color: #80deea;
}
.rfipbtn--cyan .rfipbtn__del:hover {
  background-color: #4dd0e1;
}
.rfipbtn--cyan .rfipbtn__del:focus, .rfipbtn--cyan .rfipbtn__del:active {
  outline: 1px solid #4dd0e1;
}

.rfipdropdown--cyan {
  box-shadow: 0 15px 24px rgba(0, 0, 0, 0.22), 0 19px 76px rgba(0, 0, 0, 0.3);
  color: #424242;
  background-color: #ffffff;
  border: 1px solid #4dd0e1;
}
.rfipdropdown--cyan input,
.rfipdropdown--cyan select {
  color: #424242;
}
.rfipdropdown--cyan .rfipcategory select {
  background-color: #ffffff;
  border: 0 none;
  border-bottom: 1px solid #26c6da;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--cyan .rfipcategory select:focus, .rfipdropdown--cyan .rfipcategory select:active {
  border-bottom-color: #00bcd4;
  box-shadow: 0 1px 0 0 #00bcd4;
  outline: 0 none;
}
.rfipdropdown--cyan .rfipicons__cp {
  border: 0 none;
  border-bottom: 1px solid #26c6da;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--cyan .rfipicons__cp:focus, .rfipdropdown--cyan .rfipicons__cp:active {
  border-bottom-color: #00bcd4;
  box-shadow: 0 1px 0 0 #00bcd4;
  outline: 0 none;
}
.rfipdropdown--cyan .rfipicons__left, .rfipdropdown--cyan .rfipicons__right {
  background-color: #80deea;
  border: 1px solid #80deea;
  color: #00838f;
}
.rfipdropdown--cyan .rfipicons__left:hover, .rfipdropdown--cyan .rfipicons__right:hover {
  background-color: #26c6da;
  border: 1px solid #26c6da;
}
.rfipdropdown--cyan .rfipicons__left:focus, .rfipdropdown--cyan .rfipicons__left:active, .rfipdropdown--cyan .rfipicons__right:focus, .rfipdropdown--cyan .rfipicons__right:active {
  border: 1px solid #26c6da;
}
.rfipdropdown--cyan .rfipicons__ibox {
  background-color: #b2ebf2;
  border: 1px solid #b2ebf2;
  color: #00838f;
}
.rfipdropdown--cyan .rfipicons__ibox:hover {
  background-color: #26c6da;
  border: 1px solid #26c6da;
}
.rfipdropdown--cyan .rfipicons__ibox:focus, .rfipdropdown--cyan .rfipicons__ibox:active {
  border: 1px solid #26c6da;
}
.rfipdropdown--cyan .rfipicons__ibox--error {
  color: #ef9a9a;
}
.rfipdropdown--cyan .rfipicons__icon--selected .rfipicons__ibox {
  background-color: #80deea;
}
.rfipdropdown--cyan .rfipsearch input {
  border: 0 none;
  border-bottom: 1px solid #26c6da;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--cyan .rfipsearch input:focus, .rfipdropdown--cyan .rfipsearch input:active {
  border-bottom-color: #00bcd4;
  box-shadow: 0 1px 0 0 #00bcd4;
  outline: 0 none;
}