.rfipbtn--indigo {
  background-color: #ffffff;
  border: 1px solid #7986cb;
}
.rfipbtn--indigo:active, .rfipbtn--indigo:focus {
  box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24), 0 1.5px 6px rgba(0, 0, 0, 0.12);
  border: 1px solid #9fa8da;
}
.rfipbtn--indigo .rfipbtn__button {
  border: 0 none transparent;
  border-left: 1px solid #7986cb;
  background-color: #c5cae9;
  color: #283593;
}
.rfipbtn--indigo .rfipbtn__button:hover {
  background-color: #9fa8da;
}
.rfipbtn--indigo .rfipbtn__button:active {
  box-shadow: inset 0 0 10px 0 #7986cb;
}
.rfipbtn--indigo .rfipbtn__selection {
  position: inherit;
}
.rfipbtn--indigo .rfipbtn__icon {
  border: 1px solid #9fa8da;
  color: #283593;
}
.rfipbtn--indigo .rfipbtn__icon--empty {
  color: #7986cb;
}
.rfipbtn--indigo .rfipbtn__del {
  background-color: #9fa8da;
}
.rfipbtn--indigo .rfipbtn__del:hover {
  background-color: #7986cb;
}
.rfipbtn--indigo .rfipbtn__del:focus, .rfipbtn--indigo .rfipbtn__del:active {
  outline: 1px solid #7986cb;
}

.rfipdropdown--indigo {
  box-shadow: 0 15px 24px rgba(0, 0, 0, 0.22), 0 19px 76px rgba(0, 0, 0, 0.3);
  color: #424242;
  background-color: #ffffff;
  border: 1px solid #7986cb;
}
.rfipdropdown--indigo input,
.rfipdropdown--indigo select {
  color: #424242;
}
.rfipdropdown--indigo .rfipcategory select {
  background-color: #ffffff;
  border: 0 none;
  border-bottom: 1px solid #5c6bc0;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--indigo .rfipcategory select:focus, .rfipdropdown--indigo .rfipcategory select:active {
  border-bottom-color: #3f51b5;
  box-shadow: 0 1px 0 0 #3f51b5;
  outline: 0 none;
}
.rfipdropdown--indigo .rfipicons__cp {
  border: 0 none;
  border-bottom: 1px solid #5c6bc0;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--indigo .rfipicons__cp:focus, .rfipdropdown--indigo .rfipicons__cp:active {
  border-bottom-color: #3f51b5;
  box-shadow: 0 1px 0 0 #3f51b5;
  outline: 0 none;
}
.rfipdropdown--indigo .rfipicons__left, .rfipdropdown--indigo .rfipicons__right {
  background-color: #9fa8da;
  border: 1px solid #9fa8da;
  color: #283593;
}
.rfipdropdown--indigo .rfipicons__left:hover, .rfipdropdown--indigo .rfipicons__right:hover {
  background-color: #5c6bc0;
  border: 1px solid #5c6bc0;
}
.rfipdropdown--indigo .rfipicons__left:focus, .rfipdropdown--indigo .rfipicons__left:active, .rfipdropdown--indigo .rfipicons__right:focus, .rfipdropdown--indigo .rfipicons__right:active {
  border: 1px solid #5c6bc0;
}
.rfipdropdown--indigo .rfipicons__ibox {
  background-color: #c5cae9;
  border: 1px solid #c5cae9;
  color: #283593;
}
.rfipdropdown--indigo .rfipicons__ibox:hover {
  background-color: #5c6bc0;
  border: 1px solid #5c6bc0;
}
.rfipdropdown--indigo .rfipicons__ibox:focus, .rfipdropdown--indigo .rfipicons__ibox:active {
  border: 1px solid #5c6bc0;
}
.rfipdropdown--indigo .rfipicons__ibox--error {
  color: #ef9a9a;
}
.rfipdropdown--indigo .rfipicons__icon--selected .rfipicons__ibox {
  background-color: #9fa8da;
}
.rfipdropdown--indigo .rfipsearch input {
  border: 0 none;
  border-bottom: 1px solid #5c6bc0;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--indigo .rfipsearch input:focus, .rfipdropdown--indigo .rfipsearch input:active {
  border-bottom-color: #3f51b5;
  box-shadow: 0 1px 0 0 #3f51b5;
  outline: 0 none;
}