.rfipbtn--red {
  background-color: #ffffff;
  border: 1px solid #e57373;
}
.rfipbtn--red:active, .rfipbtn--red:focus {
  box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24), 0 1.5px 6px rgba(0, 0, 0, 0.12);
  border: 1px solid #ef9a9a;
}
.rfipbtn--red .rfipbtn__button {
  border: 0 none transparent;
  border-left: 1px solid #e57373;
  background-color: #ffcdd2;
  color: #c62828;
}
.rfipbtn--red .rfipbtn__button:hover {
  background-color: #ef9a9a;
}
.rfipbtn--red .rfipbtn__button:active {
  box-shadow: inset 0 0 10px 0 #e57373;
}
.rfipbtn--red .rfipbtn__selection {
  position: inherit;
}
.rfipbtn--red .rfipbtn__icon {
  border: 1px solid #ef9a9a;
  color: #c62828;
}
.rfipbtn--red .rfipbtn__icon--empty {
  color: #e57373;
}
.rfipbtn--red .rfipbtn__del {
  background-color: #ef9a9a;
}
.rfipbtn--red .rfipbtn__del:hover {
  background-color: #e57373;
}
.rfipbtn--red .rfipbtn__del:focus, .rfipbtn--red .rfipbtn__del:active {
  outline: 1px solid #e57373;
}

.rfipdropdown--red {
  box-shadow: 0 15px 24px rgba(0, 0, 0, 0.22), 0 19px 76px rgba(0, 0, 0, 0.3);
  color: #424242;
  background-color: #ffffff;
  border: 1px solid #e57373;
}
.rfipdropdown--red input,
.rfipdropdown--red select {
  color: #424242;
}
.rfipdropdown--red .rfipcategory select {
  background-color: #ffffff;
  border: 0 none;
  border-bottom: 1px solid #ef5350;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--red .rfipcategory select:focus, .rfipdropdown--red .rfipcategory select:active {
  border-bottom-color: #f44336;
  box-shadow: 0 1px 0 0 #f44336;
  outline: 0 none;
}
.rfipdropdown--red .rfipicons__cp {
  border: 0 none;
  border-bottom: 1px solid #ef5350;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--red .rfipicons__cp:focus, .rfipdropdown--red .rfipicons__cp:active {
  border-bottom-color: #f44336;
  box-shadow: 0 1px 0 0 #f44336;
  outline: 0 none;
}
.rfipdropdown--red .rfipicons__left, .rfipdropdown--red .rfipicons__right {
  background-color: #ef9a9a;
  border: 1px solid #ef9a9a;
  color: #c62828;
}
.rfipdropdown--red .rfipicons__left:hover, .rfipdropdown--red .rfipicons__right:hover {
  background-color: #ef5350;
  border: 1px solid #ef5350;
}
.rfipdropdown--red .rfipicons__left:focus, .rfipdropdown--red .rfipicons__left:active, .rfipdropdown--red .rfipicons__right:focus, .rfipdropdown--red .rfipicons__right:active {
  border: 1px solid #ef5350;
}
.rfipdropdown--red .rfipicons__ibox {
  background-color: #ffcdd2;
  border: 1px solid #ffcdd2;
  color: #c62828;
}
.rfipdropdown--red .rfipicons__ibox:hover {
  background-color: #ef5350;
  border: 1px solid #ef5350;
}
.rfipdropdown--red .rfipicons__ibox:focus, .rfipdropdown--red .rfipicons__ibox:active {
  border: 1px solid #ef5350;
}
.rfipdropdown--red .rfipicons__ibox--error {
  color: #ef9a9a;
}
.rfipdropdown--red .rfipicons__icon--selected .rfipicons__ibox {
  background-color: #ef9a9a;
}
.rfipdropdown--red .rfipsearch input {
  border: 0 none;
  border-bottom: 1px solid #ef5350;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--red .rfipsearch input:focus, .rfipdropdown--red .rfipsearch input:active {
  border-bottom-color: #f44336;
  box-shadow: 0 1px 0 0 #f44336;
  outline: 0 none;
}