.rfipbtn--green {
  background-color: #ffffff;
  border: 1px solid #81c784;
}
.rfipbtn--green:active, .rfipbtn--green:focus {
  box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24), 0 1.5px 6px rgba(0, 0, 0, 0.12);
  border: 1px solid #a5d6a7;
}
.rfipbtn--green .rfipbtn__button {
  border: 0 none transparent;
  border-left: 1px solid #81c784;
  background-color: #c8e6c9;
  color: #2e7d32;
}
.rfipbtn--green .rfipbtn__button:hover {
  background-color: #a5d6a7;
}
.rfipbtn--green .rfipbtn__button:active {
  box-shadow: inset 0 0 10px 0 #81c784;
}
.rfipbtn--green .rfipbtn__selection {
  position: inherit;
}
.rfipbtn--green .rfipbtn__icon {
  border: 1px solid #a5d6a7;
  color: #2e7d32;
}
.rfipbtn--green .rfipbtn__icon--empty {
  color: #81c784;
}
.rfipbtn--green .rfipbtn__del {
  background-color: #a5d6a7;
}
.rfipbtn--green .rfipbtn__del:hover {
  background-color: #81c784;
}
.rfipbtn--green .rfipbtn__del:focus, .rfipbtn--green .rfipbtn__del:active {
  outline: 1px solid #81c784;
}

.rfipdropdown--green {
  box-shadow: 0 15px 24px rgba(0, 0, 0, 0.22), 0 19px 76px rgba(0, 0, 0, 0.3);
  color: #424242;
  background-color: #ffffff;
  border: 1px solid #81c784;
}
.rfipdropdown--green input,
.rfipdropdown--green select {
  color: #424242;
}
.rfipdropdown--green .rfipcategory select {
  background-color: #ffffff;
  border: 0 none;
  border-bottom: 1px solid #66bb6a;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--green .rfipcategory select:focus, .rfipdropdown--green .rfipcategory select:active {
  border-bottom-color: #4caf50;
  box-shadow: 0 1px 0 0 #4caf50;
  outline: 0 none;
}
.rfipdropdown--green .rfipicons__cp {
  border: 0 none;
  border-bottom: 1px solid #66bb6a;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--green .rfipicons__cp:focus, .rfipdropdown--green .rfipicons__cp:active {
  border-bottom-color: #4caf50;
  box-shadow: 0 1px 0 0 #4caf50;
  outline: 0 none;
}
.rfipdropdown--green .rfipicons__left, .rfipdropdown--green .rfipicons__right {
  background-color: #a5d6a7;
  border: 1px solid #a5d6a7;
  color: #2e7d32;
}
.rfipdropdown--green .rfipicons__left:hover, .rfipdropdown--green .rfipicons__right:hover {
  background-color: #66bb6a;
  border: 1px solid #66bb6a;
}
.rfipdropdown--green .rfipicons__left:focus, .rfipdropdown--green .rfipicons__left:active, .rfipdropdown--green .rfipicons__right:focus, .rfipdropdown--green .rfipicons__right:active {
  border: 1px solid #66bb6a;
}
.rfipdropdown--green .rfipicons__ibox {
  background-color: #c8e6c9;
  border: 1px solid #c8e6c9;
  color: #2e7d32;
}
.rfipdropdown--green .rfipicons__ibox:hover {
  background-color: #66bb6a;
  border: 1px solid #66bb6a;
}
.rfipdropdown--green .rfipicons__ibox:focus, .rfipdropdown--green .rfipicons__ibox:active {
  border: 1px solid #66bb6a;
}
.rfipdropdown--green .rfipicons__ibox--error {
  color: #ef9a9a;
}
.rfipdropdown--green .rfipicons__icon--selected .rfipicons__ibox {
  background-color: #a5d6a7;
}
.rfipdropdown--green .rfipsearch input {
  border: 0 none;
  border-bottom: 1px solid #66bb6a;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--green .rfipsearch input:focus, .rfipdropdown--green .rfipsearch input:active {
  border-bottom-color: #4caf50;
  box-shadow: 0 1px 0 0 #4caf50;
  outline: 0 none;
}