.rfipbtn--brown {
  background-color: #ffffff;
  border: 1px solid #a1887f;
}
.rfipbtn--brown:active, .rfipbtn--brown:focus {
  box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24), 0 1.5px 6px rgba(0, 0, 0, 0.12);
  border: 1px solid #bcaaa4;
}
.rfipbtn--brown .rfipbtn__button {
  border: 0 none transparent;
  border-left: 1px solid #a1887f;
  background-color: #d7ccc8;
  color: #4e342e;
}
.rfipbtn--brown .rfipbtn__button:hover {
  background-color: #bcaaa4;
}
.rfipbtn--brown .rfipbtn__button:active {
  box-shadow: inset 0 0 10px 0 #a1887f;
}
.rfipbtn--brown .rfipbtn__selection {
  position: inherit;
}
.rfipbtn--brown .rfipbtn__icon {
  border: 1px solid #bcaaa4;
  color: #4e342e;
}
.rfipbtn--brown .rfipbtn__icon--empty {
  color: #a1887f;
}
.rfipbtn--brown .rfipbtn__del {
  background-color: #bcaaa4;
}
.rfipbtn--brown .rfipbtn__del:hover {
  background-color: #a1887f;
}
.rfipbtn--brown .rfipbtn__del:focus, .rfipbtn--brown .rfipbtn__del:active {
  outline: 1px solid #a1887f;
}

.rfipdropdown--brown {
  box-shadow: 0 15px 24px rgba(0, 0, 0, 0.22), 0 19px 76px rgba(0, 0, 0, 0.3);
  color: #424242;
  background-color: #ffffff;
  border: 1px solid #a1887f;
}
.rfipdropdown--brown input,
.rfipdropdown--brown select {
  color: #424242;
}
.rfipdropdown--brown .rfipcategory select {
  background-color: #ffffff;
  border: 0 none;
  border-bottom: 1px solid #8d6e63;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--brown .rfipcategory select:focus, .rfipdropdown--brown .rfipcategory select:active {
  border-bottom-color: #795548;
  box-shadow: 0 1px 0 0 #795548;
  outline: 0 none;
}
.rfipdropdown--brown .rfipicons__cp {
  border: 0 none;
  border-bottom: 1px solid #8d6e63;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--brown .rfipicons__cp:focus, .rfipdropdown--brown .rfipicons__cp:active {
  border-bottom-color: #795548;
  box-shadow: 0 1px 0 0 #795548;
  outline: 0 none;
}
.rfipdropdown--brown .rfipicons__left, .rfipdropdown--brown .rfipicons__right {
  background-color: #bcaaa4;
  border: 1px solid #bcaaa4;
  color: #4e342e;
}
.rfipdropdown--brown .rfipicons__left:hover, .rfipdropdown--brown .rfipicons__right:hover {
  background-color: #8d6e63;
  border: 1px solid #8d6e63;
}
.rfipdropdown--brown .rfipicons__left:focus, .rfipdropdown--brown .rfipicons__left:active, .rfipdropdown--brown .rfipicons__right:focus, .rfipdropdown--brown .rfipicons__right:active {
  border: 1px solid #8d6e63;
}
.rfipdropdown--brown .rfipicons__ibox {
  background-color: #d7ccc8;
  border: 1px solid #d7ccc8;
  color: #4e342e;
}
.rfipdropdown--brown .rfipicons__ibox:hover {
  background-color: #8d6e63;
  border: 1px solid #8d6e63;
}
.rfipdropdown--brown .rfipicons__ibox:focus, .rfipdropdown--brown .rfipicons__ibox:active {
  border: 1px solid #8d6e63;
}
.rfipdropdown--brown .rfipicons__ibox--error {
  color: #ef9a9a;
}
.rfipdropdown--brown .rfipicons__icon--selected .rfipicons__ibox {
  background-color: #bcaaa4;
}
.rfipdropdown--brown .rfipsearch input {
  border: 0 none;
  border-bottom: 1px solid #8d6e63;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--brown .rfipsearch input:focus, .rfipdropdown--brown .rfipsearch input:active {
  border-bottom-color: #795548;
  box-shadow: 0 1px 0 0 #795548;
  outline: 0 none;
}