.rfipbtn--purple {
  background-color: #ffffff;
  border: 1px solid #ba68c8;
}
.rfipbtn--purple:active, .rfipbtn--purple:focus {
  box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24), 0 1.5px 6px rgba(0, 0, 0, 0.12);
  border: 1px solid #ce93d8;
}
.rfipbtn--purple .rfipbtn__button {
  border: 0 none transparent;
  border-left: 1px solid #ba68c8;
  background-color: #e1bee7;
  color: #6a1b9a;
}
.rfipbtn--purple .rfipbtn__button:hover {
  background-color: #ce93d8;
}
.rfipbtn--purple .rfipbtn__button:active {
  box-shadow: inset 0 0 10px 0 #ba68c8;
}
.rfipbtn--purple .rfipbtn__selection {
  position: inherit;
}
.rfipbtn--purple .rfipbtn__icon {
  border: 1px solid #ce93d8;
  color: #6a1b9a;
}
.rfipbtn--purple .rfipbtn__icon--empty {
  color: #ba68c8;
}
.rfipbtn--purple .rfipbtn__del {
  background-color: #ce93d8;
}
.rfipbtn--purple .rfipbtn__del:hover {
  background-color: #ba68c8;
}
.rfipbtn--purple .rfipbtn__del:focus, .rfipbtn--purple .rfipbtn__del:active {
  outline: 1px solid #ba68c8;
}

.rfipdropdown--purple {
  box-shadow: 0 15px 24px rgba(0, 0, 0, 0.22), 0 19px 76px rgba(0, 0, 0, 0.3);
  color: #424242;
  background-color: #ffffff;
  border: 1px solid #ba68c8;
}
.rfipdropdown--purple input,
.rfipdropdown--purple select {
  color: #424242;
}
.rfipdropdown--purple .rfipcategory select {
  background-color: #ffffff;
  border: 0 none;
  border-bottom: 1px solid #ab47bc;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--purple .rfipcategory select:focus, .rfipdropdown--purple .rfipcategory select:active {
  border-bottom-color: #9c27b0;
  box-shadow: 0 1px 0 0 #9c27b0;
  outline: 0 none;
}
.rfipdropdown--purple .rfipicons__cp {
  border: 0 none;
  border-bottom: 1px solid #ab47bc;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--purple .rfipicons__cp:focus, .rfipdropdown--purple .rfipicons__cp:active {
  border-bottom-color: #9c27b0;
  box-shadow: 0 1px 0 0 #9c27b0;
  outline: 0 none;
}
.rfipdropdown--purple .rfipicons__left, .rfipdropdown--purple .rfipicons__right {
  background-color: #ce93d8;
  border: 1px solid #ce93d8;
  color: #6a1b9a;
}
.rfipdropdown--purple .rfipicons__left:hover, .rfipdropdown--purple .rfipicons__right:hover {
  background-color: #ab47bc;
  border: 1px solid #ab47bc;
}
.rfipdropdown--purple .rfipicons__left:focus, .rfipdropdown--purple .rfipicons__left:active, .rfipdropdown--purple .rfipicons__right:focus, .rfipdropdown--purple .rfipicons__right:active {
  border: 1px solid #ab47bc;
}
.rfipdropdown--purple .rfipicons__ibox {
  background-color: #e1bee7;
  border: 1px solid #e1bee7;
  color: #6a1b9a;
}
.rfipdropdown--purple .rfipicons__ibox:hover {
  background-color: #ab47bc;
  border: 1px solid #ab47bc;
}
.rfipdropdown--purple .rfipicons__ibox:focus, .rfipdropdown--purple .rfipicons__ibox:active {
  border: 1px solid #ab47bc;
}
.rfipdropdown--purple .rfipicons__ibox--error {
  color: #ef9a9a;
}
.rfipdropdown--purple .rfipicons__icon--selected .rfipicons__ibox {
  background-color: #ce93d8;
}
.rfipdropdown--purple .rfipsearch input {
  border: 0 none;
  border-bottom: 1px solid #ab47bc;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--purple .rfipsearch input:focus, .rfipdropdown--purple .rfipsearch input:active {
  border-bottom-color: #9c27b0;
  box-shadow: 0 1px 0 0 #9c27b0;
  outline: 0 none;
}