.rfipbtn--blue {
  background-color: #ffffff;
  border: 1px solid #64b5f6;
}
.rfipbtn--blue:active, .rfipbtn--blue:focus {
  box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24), 0 1.5px 6px rgba(0, 0, 0, 0.12);
  border: 1px solid #90caf9;
}
.rfipbtn--blue .rfipbtn__button {
  border: 0 none transparent;
  border-left: 1px solid #64b5f6;
  background-color: #bbdefb;
  color: #1565c0;
}
.rfipbtn--blue .rfipbtn__button:hover {
  background-color: #90caf9;
}
.rfipbtn--blue .rfipbtn__button:active {
  box-shadow: inset 0 0 10px 0 #64b5f6;
}
.rfipbtn--blue .rfipbtn__selection {
  position: inherit;
}
.rfipbtn--blue .rfipbtn__icon {
  border: 1px solid #90caf9;
  color: #1565c0;
}
.rfipbtn--blue .rfipbtn__icon--empty {
  color: #64b5f6;
}
.rfipbtn--blue .rfipbtn__del {
  background-color: #90caf9;
}
.rfipbtn--blue .rfipbtn__del:hover {
  background-color: #64b5f6;
}
.rfipbtn--blue .rfipbtn__del:focus, .rfipbtn--blue .rfipbtn__del:active {
  outline: 1px solid #64b5f6;
}

.rfipdropdown--blue {
  box-shadow: 0 15px 24px rgba(0, 0, 0, 0.22), 0 19px 76px rgba(0, 0, 0, 0.3);
  color: #424242;
  background-color: #ffffff;
  border: 1px solid #64b5f6;
}
.rfipdropdown--blue input,
.rfipdropdown--blue select {
  color: #424242;
}
.rfipdropdown--blue .rfipcategory select {
  background-color: #ffffff;
  border: 0 none;
  border-bottom: 1px solid #42a5f5;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--blue .rfipcategory select:focus, .rfipdropdown--blue .rfipcategory select:active {
  border-bottom-color: #2196f3;
  box-shadow: 0 1px 0 0 #2196f3;
  outline: 0 none;
}
.rfipdropdown--blue .rfipicons__cp {
  border: 0 none;
  border-bottom: 1px solid #42a5f5;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--blue .rfipicons__cp:focus, .rfipdropdown--blue .rfipicons__cp:active {
  border-bottom-color: #2196f3;
  box-shadow: 0 1px 0 0 #2196f3;
  outline: 0 none;
}
.rfipdropdown--blue .rfipicons__left, .rfipdropdown--blue .rfipicons__right {
  background-color: #90caf9;
  border: 1px solid #90caf9;
  color: #1565c0;
}
.rfipdropdown--blue .rfipicons__left:hover, .rfipdropdown--blue .rfipicons__right:hover {
  background-color: #42a5f5;
  border: 1px solid #42a5f5;
}
.rfipdropdown--blue .rfipicons__left:focus, .rfipdropdown--blue .rfipicons__left:active, .rfipdropdown--blue .rfipicons__right:focus, .rfipdropdown--blue .rfipicons__right:active {
  border: 1px solid #42a5f5;
}
.rfipdropdown--blue .rfipicons__ibox {
  background-color: #bbdefb;
  border: 1px solid #bbdefb;
  color: #1565c0;
}
.rfipdropdown--blue .rfipicons__ibox:hover {
  background-color: #42a5f5;
  border: 1px solid #42a5f5;
}
.rfipdropdown--blue .rfipicons__ibox:focus, .rfipdropdown--blue .rfipicons__ibox:active {
  border: 1px solid #42a5f5;
}
.rfipdropdown--blue .rfipicons__ibox--error {
  color: #ef9a9a;
}
.rfipdropdown--blue .rfipicons__icon--selected .rfipicons__ibox {
  background-color: #90caf9;
}
.rfipdropdown--blue .rfipsearch input {
  border: 0 none;
  border-bottom: 1px solid #42a5f5;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--blue .rfipsearch input:focus, .rfipdropdown--blue .rfipsearch input:active {
  border-bottom-color: #2196f3;
  box-shadow: 0 1px 0 0 #2196f3;
  outline: 0 none;
}