.rfipbtn--orange {
  background-color: #ffffff;
  border: 1px solid #ffb74d;
}
.rfipbtn--orange:active, .rfipbtn--orange:focus {
  box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24), 0 1.5px 6px rgba(0, 0, 0, 0.12);
  border: 1px solid #ffcc80;
}
.rfipbtn--orange .rfipbtn__button {
  border: 0 none transparent;
  border-left: 1px solid #ffb74d;
  background-color: #ffe0b2;
  color: #ef6c00;
}
.rfipbtn--orange .rfipbtn__button:hover {
  background-color: #ffcc80;
}
.rfipbtn--orange .rfipbtn__button:active {
  box-shadow: inset 0 0 10px 0 #ffb74d;
}
.rfipbtn--orange .rfipbtn__selection {
  position: inherit;
}
.rfipbtn--orange .rfipbtn__icon {
  border: 1px solid #ffcc80;
  color: #ef6c00;
}
.rfipbtn--orange .rfipbtn__icon--empty {
  color: #ffb74d;
}
.rfipbtn--orange .rfipbtn__del {
  background-color: #ffcc80;
}
.rfipbtn--orange .rfipbtn__del:hover {
  background-color: #ffb74d;
}
.rfipbtn--orange .rfipbtn__del:focus, .rfipbtn--orange .rfipbtn__del:active {
  outline: 1px solid #ffb74d;
}

.rfipdropdown--orange {
  box-shadow: 0 15px 24px rgba(0, 0, 0, 0.22), 0 19px 76px rgba(0, 0, 0, 0.3);
  color: #424242;
  background-color: #ffffff;
  border: 1px solid #ffb74d;
}
.rfipdropdown--orange input,
.rfipdropdown--orange select {
  color: #424242;
}
.rfipdropdown--orange .rfipcategory select {
  background-color: #ffffff;
  border: 0 none;
  border-bottom: 1px solid #ffa726;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--orange .rfipcategory select:focus, .rfipdropdown--orange .rfipcategory select:active {
  border-bottom-color: #ff9800;
  box-shadow: 0 1px 0 0 #ff9800;
  outline: 0 none;
}
.rfipdropdown--orange .rfipicons__cp {
  border: 0 none;
  border-bottom: 1px solid #ffa726;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--orange .rfipicons__cp:focus, .rfipdropdown--orange .rfipicons__cp:active {
  border-bottom-color: #ff9800;
  box-shadow: 0 1px 0 0 #ff9800;
  outline: 0 none;
}
.rfipdropdown--orange .rfipicons__left, .rfipdropdown--orange .rfipicons__right {
  background-color: #ffcc80;
  border: 1px solid #ffcc80;
  color: #ef6c00;
}
.rfipdropdown--orange .rfipicons__left:hover, .rfipdropdown--orange .rfipicons__right:hover {
  background-color: #ffa726;
  border: 1px solid #ffa726;
}
.rfipdropdown--orange .rfipicons__left:focus, .rfipdropdown--orange .rfipicons__left:active, .rfipdropdown--orange .rfipicons__right:focus, .rfipdropdown--orange .rfipicons__right:active {
  border: 1px solid #ffa726;
}
.rfipdropdown--orange .rfipicons__ibox {
  background-color: #ffe0b2;
  border: 1px solid #ffe0b2;
  color: #ef6c00;
}
.rfipdropdown--orange .rfipicons__ibox:hover {
  background-color: #ffa726;
  border: 1px solid #ffa726;
}
.rfipdropdown--orange .rfipicons__ibox:focus, .rfipdropdown--orange .rfipicons__ibox:active {
  border: 1px solid #ffa726;
}
.rfipdropdown--orange .rfipicons__ibox--error {
  color: #ef9a9a;
}
.rfipdropdown--orange .rfipicons__icon--selected .rfipicons__ibox {
  background-color: #ffcc80;
}
.rfipdropdown--orange .rfipsearch input {
  border: 0 none;
  border-bottom: 1px solid #ffa726;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--orange .rfipsearch input:focus, .rfipdropdown--orange .rfipsearch input:active {
  border-bottom-color: #ff9800;
  box-shadow: 0 1px 0 0 #ff9800;
  outline: 0 none;
}