.rfipbtn--pink {
  background-color: #ffffff;
  border: 1px solid #f06292;
}
.rfipbtn--pink:active, .rfipbtn--pink:focus {
  box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24), 0 1.5px 6px rgba(0, 0, 0, 0.12);
  border: 1px solid #f48fb1;
}
.rfipbtn--pink .rfipbtn__button {
  border: 0 none transparent;
  border-left: 1px solid #f06292;
  background-color: #f8bbd0;
  color: #ad1457;
}
.rfipbtn--pink .rfipbtn__button:hover {
  background-color: #f48fb1;
}
.rfipbtn--pink .rfipbtn__button:active {
  box-shadow: inset 0 0 10px 0 #f06292;
}
.rfipbtn--pink .rfipbtn__selection {
  position: inherit;
}
.rfipbtn--pink .rfipbtn__icon {
  border: 1px solid #f48fb1;
  color: #ad1457;
}
.rfipbtn--pink .rfipbtn__icon--empty {
  color: #f06292;
}
.rfipbtn--pink .rfipbtn__del {
  background-color: #f48fb1;
}
.rfipbtn--pink .rfipbtn__del:hover {
  background-color: #f06292;
}
.rfipbtn--pink .rfipbtn__del:focus, .rfipbtn--pink .rfipbtn__del:active {
  outline: 1px solid #f06292;
}

.rfipdropdown--pink {
  box-shadow: 0 15px 24px rgba(0, 0, 0, 0.22), 0 19px 76px rgba(0, 0, 0, 0.3);
  color: #424242;
  background-color: #ffffff;
  border: 1px solid #f06292;
}
.rfipdropdown--pink input,
.rfipdropdown--pink select {
  color: #424242;
}
.rfipdropdown--pink .rfipcategory select {
  background-color: #ffffff;
  border: 0 none;
  border-bottom: 1px solid #ec407a;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--pink .rfipcategory select:focus, .rfipdropdown--pink .rfipcategory select:active {
  border-bottom-color: #e91e63;
  box-shadow: 0 1px 0 0 #e91e63;
  outline: 0 none;
}
.rfipdropdown--pink .rfipicons__cp {
  border: 0 none;
  border-bottom: 1px solid #ec407a;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--pink .rfipicons__cp:focus, .rfipdropdown--pink .rfipicons__cp:active {
  border-bottom-color: #e91e63;
  box-shadow: 0 1px 0 0 #e91e63;
  outline: 0 none;
}
.rfipdropdown--pink .rfipicons__left, .rfipdropdown--pink .rfipicons__right {
  background-color: #f48fb1;
  border: 1px solid #f48fb1;
  color: #ad1457;
}
.rfipdropdown--pink .rfipicons__left:hover, .rfipdropdown--pink .rfipicons__right:hover {
  background-color: #ec407a;
  border: 1px solid #ec407a;
}
.rfipdropdown--pink .rfipicons__left:focus, .rfipdropdown--pink .rfipicons__left:active, .rfipdropdown--pink .rfipicons__right:focus, .rfipdropdown--pink .rfipicons__right:active {
  border: 1px solid #ec407a;
}
.rfipdropdown--pink .rfipicons__ibox {
  background-color: #f8bbd0;
  border: 1px solid #f8bbd0;
  color: #ad1457;
}
.rfipdropdown--pink .rfipicons__ibox:hover {
  background-color: #ec407a;
  border: 1px solid #ec407a;
}
.rfipdropdown--pink .rfipicons__ibox:focus, .rfipdropdown--pink .rfipicons__ibox:active {
  border: 1px solid #ec407a;
}
.rfipdropdown--pink .rfipicons__ibox--error {
  color: #ef9a9a;
}
.rfipdropdown--pink .rfipicons__icon--selected .rfipicons__ibox {
  background-color: #f48fb1;
}
.rfipdropdown--pink .rfipsearch input {
  border: 0 none;
  border-bottom: 1px solid #ec407a;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--pink .rfipsearch input:focus, .rfipdropdown--pink .rfipsearch input:active {
  border-bottom-color: #e91e63;
  box-shadow: 0 1px 0 0 #e91e63;
  outline: 0 none;
}