.rfipbtn--default {
  background-color: #ffffff;
  border: 1px solid #e0e0e0;
}
.rfipbtn--default:active, .rfipbtn--default:focus {
  box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24), 0 1.5px 6px rgba(0, 0, 0, 0.12);
  border: 1px solid #eeeeee;
}
.rfipbtn--default .rfipbtn__button {
  border: 0 none transparent;
  border-left: 1px solid #e0e0e0;
  background-color: #f5f5f5;
  color: #424242;
}
.rfipbtn--default .rfipbtn__button:hover {
  background-color: #eeeeee;
}
.rfipbtn--default .rfipbtn__button:active {
  box-shadow: inset 0 0 10px 0 #e0e0e0;
}
.rfipbtn--default .rfipbtn__selection {
  position: inherit;
}
.rfipbtn--default .rfipbtn__icon {
  border: 1px solid #eeeeee;
  color: #424242;
}
.rfipbtn--default .rfipbtn__icon--empty {
  color: #e0e0e0;
}
.rfipbtn--default .rfipbtn__del {
  background-color: #eeeeee;
}
.rfipbtn--default .rfipbtn__del:hover {
  background-color: #e0e0e0;
}
.rfipbtn--default .rfipbtn__del:focus, .rfipbtn--default .rfipbtn__del:active {
  outline: 1px solid #e0e0e0;
}

.rfipdropdown--default {
  box-shadow: 0 15px 24px rgba(0, 0, 0, 0.22), 0 19px 76px rgba(0, 0, 0, 0.3);
  color: #424242;
  background-color: #ffffff;
  border: 1px solid #e0e0e0;
}
.rfipdropdown--default input,
.rfipdropdown--default select {
  color: #424242;
}
.rfipdropdown--default .rfipcategory select {
  background-color: #ffffff;
  border: 0 none;
  border-bottom: 1px solid #bdbdbd;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--default .rfipcategory select:focus, .rfipdropdown--default .rfipcategory select:active {
  border-bottom-color: #9e9e9e;
  box-shadow: 0 1px 0 0 #9e9e9e;
  outline: 0 none;
}
.rfipdropdown--default .rfipicons__cp {
  border: 0 none;
  border-bottom: 1px solid #bdbdbd;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--default .rfipicons__cp:focus, .rfipdropdown--default .rfipicons__cp:active {
  border-bottom-color: #9e9e9e;
  box-shadow: 0 1px 0 0 #9e9e9e;
  outline: 0 none;
}
.rfipdropdown--default .rfipicons__left, .rfipdropdown--default .rfipicons__right {
  background-color: #eeeeee;
  border: 1px solid #eeeeee;
  color: #424242;
}
.rfipdropdown--default .rfipicons__left:hover, .rfipdropdown--default .rfipicons__right:hover {
  background-color: #bdbdbd;
  border: 1px solid #bdbdbd;
}
.rfipdropdown--default .rfipicons__left:focus, .rfipdropdown--default .rfipicons__left:active, .rfipdropdown--default .rfipicons__right:focus, .rfipdropdown--default .rfipicons__right:active {
  border: 1px solid #bdbdbd;
}
.rfipdropdown--default .rfipicons__ibox {
  background-color: #f5f5f5;
  border: 1px solid #f5f5f5;
  color: #424242;
}
.rfipdropdown--default .rfipicons__ibox:hover {
  background-color: #bdbdbd;
  border: 1px solid #bdbdbd;
}
.rfipdropdown--default .rfipicons__ibox:focus, .rfipdropdown--default .rfipicons__ibox:active {
  border: 1px solid #bdbdbd;
}
.rfipdropdown--default .rfipicons__ibox--error {
  color: #ef9a9a;
}
.rfipdropdown--default .rfipicons__icon--selected .rfipicons__ibox {
  background-color: #eeeeee;
}
.rfipdropdown--default .rfipsearch input {
  border: 0 none;
  border-bottom: 1px solid #bdbdbd;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--default .rfipsearch input:focus, .rfipdropdown--default .rfipsearch input:active {
  border-bottom-color: #9e9e9e;
  box-shadow: 0 1px 0 0 #9e9e9e;
  outline: 0 none;
}