.rfipbtn--bluegrey {
  background-color: #ffffff;
  border: 1px solid #90a4ae;
}
.rfipbtn--bluegrey:active, .rfipbtn--bluegrey:focus {
  box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24), 0 1.5px 6px rgba(0, 0, 0, 0.12);
  border: 1px solid #b0bec5;
}
.rfipbtn--bluegrey .rfipbtn__button {
  border: 0 none transparent;
  border-left: 1px solid #90a4ae;
  background-color: #cfd8dc;
  color: #37474f;
}
.rfipbtn--bluegrey .rfipbtn__button:hover {
  background-color: #b0bec5;
}
.rfipbtn--bluegrey .rfipbtn__button:active {
  box-shadow: inset 0 0 10px 0 #90a4ae;
}
.rfipbtn--bluegrey .rfipbtn__selection {
  position: inherit;
}
.rfipbtn--bluegrey .rfipbtn__icon {
  border: 1px solid #b0bec5;
  color: #37474f;
}
.rfipbtn--bluegrey .rfipbtn__icon--empty {
  color: #90a4ae;
}
.rfipbtn--bluegrey .rfipbtn__del {
  background-color: #b0bec5;
}
.rfipbtn--bluegrey .rfipbtn__del:hover {
  background-color: #90a4ae;
}
.rfipbtn--bluegrey .rfipbtn__del:focus, .rfipbtn--bluegrey .rfipbtn__del:active {
  outline: 1px solid #90a4ae;
}

.rfipdropdown--bluegrey {
  box-shadow: 0 15px 24px rgba(0, 0, 0, 0.22), 0 19px 76px rgba(0, 0, 0, 0.3);
  color: #424242;
  background-color: #ffffff;
  border: 1px solid #90a4ae;
}
.rfipdropdown--bluegrey input,
.rfipdropdown--bluegrey select {
  color: #424242;
}
.rfipdropdown--bluegrey .rfipcategory select {
  background-color: #ffffff;
  border: 0 none;
  border-bottom: 1px solid #78909c;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--bluegrey .rfipcategory select:focus, .rfipdropdown--bluegrey .rfipcategory select:active {
  border-bottom-color: #607d8b;
  box-shadow: 0 1px 0 0 #607d8b;
  outline: 0 none;
}
.rfipdropdown--bluegrey .rfipicons__cp {
  border: 0 none;
  border-bottom: 1px solid #78909c;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--bluegrey .rfipicons__cp:focus, .rfipdropdown--bluegrey .rfipicons__cp:active {
  border-bottom-color: #607d8b;
  box-shadow: 0 1px 0 0 #607d8b;
  outline: 0 none;
}
.rfipdropdown--bluegrey .rfipicons__left, .rfipdropdown--bluegrey .rfipicons__right {
  background-color: #b0bec5;
  border: 1px solid #b0bec5;
  color: #37474f;
}
.rfipdropdown--bluegrey .rfipicons__left:hover, .rfipdropdown--bluegrey .rfipicons__right:hover {
  background-color: #78909c;
  border: 1px solid #78909c;
}
.rfipdropdown--bluegrey .rfipicons__left:focus, .rfipdropdown--bluegrey .rfipicons__left:active, .rfipdropdown--bluegrey .rfipicons__right:focus, .rfipdropdown--bluegrey .rfipicons__right:active {
  border: 1px solid #78909c;
}
.rfipdropdown--bluegrey .rfipicons__ibox {
  background-color: #cfd8dc;
  border: 1px solid #cfd8dc;
  color: #37474f;
}
.rfipdropdown--bluegrey .rfipicons__ibox:hover {
  background-color: #78909c;
  border: 1px solid #78909c;
}
.rfipdropdown--bluegrey .rfipicons__ibox:focus, .rfipdropdown--bluegrey .rfipicons__ibox:active {
  border: 1px solid #78909c;
}
.rfipdropdown--bluegrey .rfipicons__ibox--error {
  color: #ef9a9a;
}
.rfipdropdown--bluegrey .rfipicons__icon--selected .rfipicons__ibox {
  background-color: #b0bec5;
}
.rfipdropdown--bluegrey .rfipsearch input {
  border: 0 none;
  border-bottom: 1px solid #78909c;
  transition: box-shadow 250ms, border 250ms;
}
.rfipdropdown--bluegrey .rfipsearch input:focus, .rfipdropdown--bluegrey .rfipsearch input:active {
  border-bottom-color: #607d8b;
  box-shadow: 0 1px 0 0 #607d8b;
  outline: 0 none;
}